import './FabricanteHorarioAtencion.css'

import { IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import { SnapshotOut } from 'mobx-state-tree'
import React, { useRef, useState } from 'react'
import * as FIcon from 'react-feather'
import InputMask from 'react-input-mask'
import { RouteComponentProps } from 'react-router'

import { FooterFormulario, ModalInfo, Page } from '../../components'
import { SubHeader } from '../../components/SubHeader'
import { DIAS_APERTURA, RUTAS_APP_FABRICANTE } from '../../constants'
import { actualizarDatosInicioFabricante, useApiToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { Fabricante as FabricanteModel } from '../../models/types'
import { Feriado as FeriadoApi } from '../../services/api'
import { ICONOS } from '../../helpers-ts/iconos'

const moment = require('moment')

interface IHorario {
  hora_apertura: string
  hora_cierre: string
  dias_apertura: string[]
}

const ErrorHorarioDefault = {
  hora_apertura: '',
  hora_cierre: '',
  dias_apertura: '',
}

export const FabricanteHorarioAtencion: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const { fabricante } = loginStore
  const [Fabricante, setFabricante] = useState<SnapshotOut<typeof FabricanteModel> | null>()
  const [DiasAtencion, setDiasAtencion] = useState('')
  const [Loading, setLoading] = useState(false)
  const [HorarioInput, setHorarioInput] = useState<IHorario>()
  const [ErrorHorario, setErrorHorario] = useState(ErrorHorarioDefault)
  const [ModalHorario, setModalHorario] = useState(false)
  const [Feriados, setFeridados] = useState<FeriadoApi[]>()
  const [Paginacion, setPaginacion] = useState({ pagina: 1, paginas: 1, pageSize: 10 })
  const paginaActual = useRef(1)
  const apiToast = useApiToaster()
  const [HeightDiv, setHeightDiv] = useState<number>(window.innerWidth <= 500 ? window.innerHeight - 300 : 800)
  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)

  React.useEffect(() => {
    if (loginStore.fabricante_id) {
      obtenerFabricante(loginStore.fabricante_id)
      obtenerFeriados()
    }
  }, [])

  React.useEffect(() => {
    if (fabricante) {
      setFabricante(null)
      const timeout = setTimeout(() => setFabricante(fabricante), 100)
      return () => clearTimeout(timeout)
    }
  }, [fabricante])

  React.useEffect(() => {
    if (Fabricante) {
      setHorarioInput({
        hora_apertura: Fabricante.hora_apertura ? Fabricante.hora_apertura : '07:00',
        hora_cierre: Fabricante.hora_cierre ? Fabricante.hora_cierre : '20:00',
        dias_apertura: Fabricante.dias_apertura ? [...Fabricante.dias_apertura] : [],
      })
      const Dias = DIAS_APERTURA.filter((dia) => {
        let isSelected =
          Fabricante?.dias_apertura && Fabricante?.dias_apertura.length > 0
            ? Fabricante?.dias_apertura.findIndex((curr) => curr === dia.id)
            : -1
        if (isSelected !== -1) {
          return true
        }
        return false
      })
      if (Dias && Dias.length > 0) {
        setDiasAtencion(Dias.map((dia) => dia.dia).join(', '))
      }
    }
  }, [Fabricante])

  React.useEffect(() => {}, [Paginacion])

  const setHorario = (campo: string, dato: any) => {
    const horario = { ...HorarioInput }
    //@ts-ignore
    horario[campo] = dato
    //@ts-ignore
    setHorarioInput(horario)
  }

  const actualizarHorarioAtencion = () => {
    const datos = { ...HorarioInput }
    //@ts-ignore
    datos['id'] = Fabricante.id
    setLoading(true)
    setErrorHorario({} as any)
    rootStore.environment.api.guardarFabricante(datos).then((result) => {
      if (result.kind === 'ok') {
        loginStore.setFabricante(result.fabricante)
        setFabricante(result.fabricante as any)
        setModalHorario(false)
        actualizarDatosInicioFabricante(rootStore)
      } else if (result.kind === 'bad-data') {
        setErrorHorario(result.errors)
      }
      apiToast(result, 'Horario guardado correctamente', 'guardar')
      setLoading(false)
    })
  }

  const obtenerFabricante = (idFabricante: number) => {
    rootStore.environment.api.adminGetFabricante(idFabricante).then((result) => {
      if (result.kind === 'ok') {
        loginStore.setFabricante(result.fabricante)
        setFabricante(result.fabricante as any)
      }
    })
  }
  React.useEffect(() => {
    if (paginaActual.current < Paginacion.pagina) {
      paginaActual.current = Paginacion.pagina
      obtenerFeriados()
    }
  }, [Paginacion])

  const obtenerFeriados = () => {
    setLoading(true)
    rootStore.environment.api.adminGetFeriados(Paginacion.pagina, Paginacion.pageSize).then((result) => {
      const paginacion = { ...Paginacion }
      setLoading(false)
      if (result.kind === 'ok') {
        const data = result.data as any
        if (!!Feriados) {
          setFeridados([...Feriados, ...data.results])
        } else {
          setFeridados([...data.results])
        }
        //
        paginacion.paginas = Math.ceil(data.count / paginacion.pageSize)
        if (paginacion.pagina > paginacion.paginas) {
          paginacion.pagina = paginacion.paginas
        }
        setPaginacion(paginacion)
      }
    })
  }

  return (
    <Page login_required fabricante={true} disableScroll={true} id={'horario-atencion-fabricante'}>
      <ModalInfo
        close={() => setModalHorario(false)}
        isOpen={ModalHorario}
        titulo="Horario de atención"
        color="var(--azul-fabricante)"
      >
        {!!HorarioInput && (
          <div>
            <div className="py-3">
              <h4>Apertura</h4>
              <div className="row justify-content-center form-control-container">
                <div className="col-12 col-md-4 py-2">
                  <InputMask
                    placeholder={''}
                    className={'form-control registro'}
                    mask="99:99"
                    onChange={(event: any) => setHorario('hora_apertura', event.target.value)}
                    value={HorarioInput.hora_apertura}
                  />
                </div>
              </div>
              {!!ErrorHorario.hora_apertura && <div className={'text-danger mt-2'}>{ErrorHorario.hora_apertura}</div>}
            </div>
            <div className="py-3">
              <h4>Cierre</h4>
              <div className="row justify-content-center py-2">
                <div className="col-12 col-md-4 form-control-container">
                  <InputMask
                    placeholder={''}
                    className={'form-control registro'}
                    mask="99:99"
                    onChange={(event: any) => setHorario('hora_cierre', event.target.value)}
                    value={HorarioInput.hora_cierre}
                  />
                </div>
              </div>
              {!!ErrorHorario.hora_cierre && <div className={'text-danger mt-2'}>{ErrorHorario.hora_cierre}</div>}
            </div>
            <h4>Días de atención</h4>
            <div className="row justify-content-center">
              <div className="col-12">
                {!!HorarioInput && (
                  <div className="d-flex justify-content-between py-3 ">
                    {DIAS_APERTURA.map((dia) => {
                      let isSelected =
                        HorarioInput?.dias_apertura && HorarioInput?.dias_apertura.length > 0
                          ? HorarioInput?.dias_apertura.findIndex((curr) => curr === dia.id)
                          : -1

                      return (
                        <a
                          key={dia.id}
                          onClick={() => {
                            const diasSeleccionados = [...HorarioInput?.dias_apertura]
                            if (isSelected !== -1) {
                              diasSeleccionados.splice(isSelected, 1)
                            } else {
                              diasSeleccionados.push(dia.id)
                            }
                            setHorario('dias_apertura', [...diasSeleccionados])
                          }}
                          className="dia-apertura"
                          style={isSelected !== -1 ? { color: 'var(--azul-fabricante)' } : {}}
                        >
                          <strong>{dia.dia[0]}</strong>
                        </a>
                      )
                    })}
                  </div>
                )}
                {!!ErrorHorario.dias_apertura && <div className={'text-danger mt-2'}>{ErrorHorario.dias_apertura}</div>}
              </div>
            </div>
            <div className="row justify-content-center">
              <div className="col-4">
                <button className="btn btn-primary" onClick={() => actualizarHorarioAtencion()}>
                  {!Loading ? 'Guardar' : <IonSpinner />}
                </button>
              </div>
            </div>
          </div>
        )}
      </ModalInfo>
      <SubHeader
        links={[
          { titulo: 'Configuración', ruta: RUTAS_APP_FABRICANTE.CONFIGURACION },
          { titulo: 'Horario de atención' },
        ]}
      />
      {!!Fabricante && (
        <div className="container-sm pt-3">
          <div className="horario-atencion">
            <div className="row">
              <div className="col-12">
                <div className="d-flex justify-content-between">
                  <div>
                    <h2 style={{ fontWeight: 'bold', color: 'var(--gris-oscuro)' }}> Horario de atención</h2>
                  </div>
                  <div>
                    {!Movil && (
                      <a onClick={() => setModalHorario(true)}>
                        <h3>Editar</h3>
                      </a>
                    )}
                  </div>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-6 col-md-3">
                <span>
                  <strong>Apertura:</strong> {moment(Fabricante.hora_apertura, 'HH:mm:ss').format('HH:mm')}
                </span>
              </div>
              <div className="col-6 col-md-3">
                <span>
                  <strong>Cierre:</strong> {moment(Fabricante.hora_cierre, 'HH:mm:ss').format('HH:mm')}
                </span>
              </div>
            </div>
            <div className="row">
              <div className="col-12">
                <div>
                  <strong>Días de atención:</strong> {DiasAtencion}
                </div>
              </div>
            </div>
          </div>
          <div className="feriados">
            <div>
              <h2 style={{ fontWeight: 'bold', color: 'var(--gris-oscuro)' }}> Cierre por feriados</h2>
            </div>
            {Feriados && Feriados.length < 1 && <div>No se encontraron registros</div>}
            {!Loading &&
              !!Feriados &&
              Feriados.length > 0 &&
              Feriados.map((feriado) => (
                <div key={feriado.id} className=" row feriado pb-2">
                  <div className="col-6">
                    <p>
                      <span style={{ textTransform: 'capitalize' }}>
                        {moment(feriado.fecha_inicio).format('dddd D MMM')}{' '}
                      </span>
                      {feriado.fecha_fin !== feriado.fecha_inicio && (
                        <>
                          {' '}
                          al{' '}
                          <span style={{ textTransform: 'capitalize' }}>
                            {moment(feriado.fecha_fin).format('dddd D MMM')}
                          </span>
                        </>
                      )}
                    </p>
                  </div>
                  <div className="col-4">
                    <p style={{ fontWeight: 'bold' }}>{feriado.nombre}</p>
                  </div>
                  <div className="col-2 text-right">
                    {!!feriado.id && (
                      <>
                        <a
                          aria-label="Editar"
                          onClick={() => history.push(RUTAS_APP_FABRICANTE.FERIADO_UPDATE(feriado.id))}
                        >
                          {ICONOS.editar}
                        </a>
                      </>
                    )}
                  </div>
                </div>
              ))}
            {Loading && (
              <div className="d-flex justify-content-center">
                <IonSpinner />
              </div>
            )}

            {!Loading && Paginacion.pagina < Paginacion.paginas && (
              <div style={{ textAlign: 'center' }}>
                <a style={{ color: 'var(--azul-fabricante)' }}>+ Ver más</a>
              </div>
            )}
          </div>
        </div>
      )}
      {!Loading && (
        <FooterFormulario
          style={{ paddingRight: 0, paddingLeft: 0, backgroundColor: Movil ? 'var(--gris-claro)' : '#fff' }}
          movil={Movil}
        >
          <div className="container-sm py-2">
            <div className="row">
              <div className={Movil ? 'col-6' : ''}>
                <button
                  className="btn btn-primary w-100 px-4"
                  onClick={() => history.push(RUTAS_APP_FABRICANTE.FERIADO)}
                >
                  Añadir
                </button>
              </div>

              {Movil && (
                <div className="col-6">
                  <button className="btn btn-primary w-100" onClick={() => setModalHorario(true)}>
                    Editar Horario
                  </button>
                </div>
              )}
            </div>
          </div>
        </FooterFormulario>
      )}
    </Page>
  )
})

export default FabricanteHorarioAtencion
